@import url('https://fonts.googleapis.com/css?family=Big+Shoulders+Display&display=swap');
html {
    font-size: 62.5%;
    font-family: 'Big Shoulders Display', cursive;
    color: #fff;
}

body {
    font-size: 2.5rem;
    background-color: #888;
}

.header {
    color: var(--main-color);
    font-size: 5rem;
    padding: 1.5rem;
}

.my-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 80vh;
    /* max-width: 1160px; */
    /* width: 80vw; */
    /* margin: auto; */
    padding: 10rem 0em 0em 0em;
    text-align: center;
    /* background-image: url('./media/computer.jpg'); */
    background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('./media/computer.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    /* background-size: auto; */
    /* opacity: .5;  */
}

.hero {
    max-width: 550px;
    padding: 2rem;
    padding-bottom: 8rem;
}

.hero a {
    color: #CED5DA;
}

.main-projects {
    padding-top: 8rem;
    width: 100%;
    background-color: #F7F7F7;
    color: #000;
}

.sub-header {
    font-size: 4rem;
    padding: 1rem;
}